import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function enterpriseValueTtm(tableName: string) {
  const title = "Enterprise Value (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["enterpriseValueTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Total company value (debt + equity - cash).",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about Enterprise Value?`,
              value: info.row.original.financialData.companyRatios?.data?.enterpriseValueTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.enterpriseValueTtm}
        />
      )
    },
  })
}

export { enterpriseValueTtm }
